
import React, {useState, useEffect} from 'react'
import "./assets/css/Favourite.css"
import TinderCard from "react-tinder-card"
import ReactPlayer from 'react-player'
import axios from "./components/api/axios"
import { Container } from 'reactstrap';

function TinderCards() {    
       const [cover, setCover] = useState([]);
       useEffect(() => {
           async function fetchData()
     { 
         const req = await axios.get ("/tec.no/card")
         setCover(req.data);
           }
           fetchData();
       }, [])
       const swiped = (direction, nameToDelete) =>{
           console.log("removing" + nameToDelete);
           /*setLastDirection(direction)*/
       }       
       const outOfFrame = (name) =>{
           console.log(name + "left the screen");
       }
       return(
           <Container>
            <main>

           <div class="cardText"><p>My favourite Selections.
                Please swipe in any direction to see more!</p> 
            </div> 
       <div className="Cards">       
        {cover.map((picture)=>(
                <TinderCard
                className="swipe"
                key={picture.name}
                preventSwipe={["up","down"]}
                onSwipe={(dir) => swiped(dir, picture.name)}
                onCardLeftScreen={()=>outOfFrame(picture.name)}       
                >
        <ReactPlayer    
            className="player" 
            url={picture.videoUrl} 
            width='320px'
            height='210px'
            controls="true" /> 
        </TinderCard>
            ))}            
            </div>
        </main>
</Container>
      
    );
}

export default TinderCards
