import React, { useState } from 'react';
import {Container, Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink } from 'reactstrap';
import '../bootstrap/dist/css/bootstrap.min.css'


const Bar = (props) => {
  const [collapsed, setCollapsed] = useState(true);

  const toggleNavbar = () => setCollapsed(!collapsed);

  return (
    <div>
        <Container>
        <Navbar color="dark" dark>
        <NavbarBrand href="/" className="mr-auto">TECHNO ONLY</NavbarBrand>
        <NavbarToggler onClick={toggleNavbar} className="mr-2" />
        <Collapse isOpen={!collapsed} navbar>
          <Nav navbar>
          <NavItem>
              <NavLink href="/"><i class="lar la-chart-bar"></i> Techno Top 20</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/cards/"><i class="lar la-heart"></i> Favourite selections</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/search/"><i class="lab la-youtube"></i> Search for Techno</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/about/"><i class="las la-info"></i> About</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
      </Container>
    </div>
  );
}

export default Bar;
