import React       from "react";
import './App.css';
// import Header from "./Header.js";
import Cards from "./Cards";
import SearchSomeYoutbe from "./SearchSomeYoutube";
import MiddleComponentSpotify from "./components/api/middleComponentSpotify"
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Container } from 'reactstrap';
import Bar from "./components/navbar"
import About from "./components/about"
 


function App() {
  return (
    
    <div className="app">
      <Container>
      <Router>
    <Bar/>
  <Switch> 
      <main>
      
      <Route exact path="/" component={MiddleComponentSpotify} />
      <Route path="/cards" component ={Cards}  /> 
    
      <Route path="/search" component ={SearchSomeYoutbe}  />
      <Route path="/about" component ={About}  />   

      </main>
 </Switch>

  </Router>
  </Container>
    </div>
  );
}

export default App;
